@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;1,100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Orbitron&family=Poppins:ital,wght@0,100;0,200;1,100&display=swap');

*{
  margin: 0;
  box-sizing: border-box;
  &:after,
  &::before{
    box-sizing: border-box;
  }
}
html,
body{
  overflow-x: hidden;
}

body{
  position: relative;
  font-family: "Poppins", sans-serif;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

button,
input,
textarea,
select{
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6{
  overflow-wrap: break-word;
}

img,
video,
canvas,
svg {
  max-width: 100%;
}

a{
  text-decoration: none;
  color: inherit;
}

ul{
  list-style: none;
}